import { Input } from 'antd'
import React from 'react'
import { CopyToClipboard } from 'react-copy-to-clipboard'

import randomNationId from '../../../libraries/randomNationId'

const { Search } = Input
class Da extends React.Component {
  state = {
    value: 'hello world',
    copied: false,
  }
  handleOnSearch() {
    var introduction = ['Hello', 'I', 'am', 'Sarah']
    var [greeting, pronoun] = introduction
    const d = randomNationId(1)
    console.log(d)
    // this.setState({ value: nationId })
  }
  render() {
    return (
      <div>
        <CopyToClipboard
          text={this.state.value}
          onCopy={() => this.setState({ copied: true })}
        >
          <Search
            placeholder="input search text"
            enterButton="Search"
            readOnly
            value={this.state.value}
            size="large"
            onSearch={() => this.handleOnSearch()}
          />
        </CopyToClipboard>

        {this.state.copied ? (
          <span style={{ color: 'red' }}>Copied.</span>
        ) : null}
      </div>
    )
  }
}

export default Da
